@import url('https://fonts.googleapis.com/css2?family=Fjalla+One&display=swap');

body {
  background: url('assets/bg/background.gif') no-repeat center center fixed; 
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
}

.main {
  outline: none;
  background-color: #13121299;
  display: flex;
  flex-direction: column;
  border-radius: 8px;
  box-shadow: 8px 8px 8px rgba(32, 7, 7, 0.144);
}

.TerminalText {
  padding-left: 10px;
  padding-top: 0px;
  color: white;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  font-size: 1.6vh;
  font-family: 'Fjalla One', sans-serif;
}